import React from 'react';
import { graphql } from 'gatsby';

import Layout from '../components/layout';
import SEO from '../components/seo';

interface Props {
  pageContext: {
    locale: string;
    canonical: boolean;
    alternatePages: {
      slug: string;
      locale: string;
    }[];
  };
  data: {
    markdownRemark: {
      html: string;
      frontmatter: {
        title: string;
        thumbnail: string;
      };
    };
  };
}

const UseCase = ({ pageContext, data }: Props) => {
  const { locale, canonical, alternatePages } = pageContext;
  const post = data.markdownRemark;
  return (
    <>
      <SEO
        title={post.frontmatter.title}
        image={post.frontmatter.thumbnail}
        locale={locale}
        canonical={canonical}
        alternatePages={alternatePages}
      />
      <Layout locale={locale}>
        <div>
          <div className="w-full">
            <h1 className="absolute text-white text-7xl w-full text-center top-48 md:text-9xl ">
              {post.frontmatter.title}
            </h1>
            <img
              src={post.frontmatter.thumbnail}
              className="object-cover object-bottom h-screen w-full"
            />
          </div>
          <div className="container py-24 dark:text-grey">
            <div className="use-case-content" dangerouslySetInnerHTML={{ __html: post.html }} />
          </div>
        </div>
      </Layout>
    </>
  );
};

export default UseCase;

export const query = graphql`
  query ($localizedSlug: String!) {
    markdownRemark(fields: { localizedSlug: { eq: $localizedSlug } }) {
      html
      frontmatter {
        title
        thumbnail
      }
    }
  }
`;
